/**RESET CSS**/
@import "reset.scss";
/**END RESET CSS**/
.ic-live {font-size: 9px;text-transform: uppercase;height: 15px;line-height: 15px;display: inline-block;vertical-align: middle;color: #e78dac;}
.ic-live:before {-webkit-border-radius: 50%;border-radius: 50%;display: block;position: relative;width: 15px;height: 15px;content: ''; background: #e78dac;box-shadow: 0 0 0 rgba(231,141,172, 0.4); margin-right: 5px;vertical-align: initial;-webkit-animation: live-pulse 1s infinite;-moz-animation: live-pulse 1s infinite;-o-animation: live-pulse 1s infinite;animation: live-pulse 1s infinite;float: left;}

/*@-webkit-keyframes live-pulse{0%{background-color:#e78dac}50%{background-color:white}100%{background-color:#e78dac}}@-moz-keyframes live-pulse{0%{background-color:#e78dac}50%{background-color:white}100%{background-color:#e78dac}}@keyframes live-pulse{0%{background-color:#e78dac}50%{background-color:white}100%{background-color:#e78dac}}*/
@-webkit-keyframes live-pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(231,141,172, 0.4);
  }
  70% {
      -webkit-box-shadow: 0 0 0 10px rgba(231,141,172, 0);
  }
  100% {
      -webkit-box-shadow: 0 0 0 0 rgba(231,141,172, 0);
  }
}
@keyframes live-pulse {
  0% {
    -moz-box-shadow: 0 0 0 0 rgba(231,141,172, 0.4);
    box-shadow: 0 0 0 0 rgba(231,141,172, 0.4);
  }
  70% {
      -moz-box-shadow: 0 0 0 10px rgba(231,141,172, 0);
      box-shadow: 0 0 0 10px rgba(231,141,172, 0);
  }
  100% {
      -moz-box-shadow: 0 0 0 0 rgba(231,141,172, 0);
      box-shadow: 0 0 0 0 rgba(231,141,172, 0);
  }
}
@-webkit-keyframes timeline {
  0% {
    background-position: -350px 0; }
  100% {
    background-position: 400px 0; } }
@keyframes timeline {
  0% {
    background-position: -350px 0; }
  100% {
    background-position: 400px 0; } }
