/**BREADCRUM**/
.section_nav{border-bottom: 1px solid #e7e7e7; height: 40px; background: #fff;
    &.noborder{border-bottom: none;}
    .btn_control_menu{width: 40px; height: 40px; line-height: 40px !important; display: none;}
    &.nav_cloned{padding-left: 40px;
        box-shadow: 0 1px 3px rgba(80,80,80,0.2), 0 3px 5px rgba(80,80,80,0.15);
        @include cubic-transition($delay: 0, $duration: 250ms, $property: (all)); 
        -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
        transform: translateY(0);
        &.nav-up{
                -webkit-transform: translateY(-100%);
                -ms-transform: translateY(-100%);
                transform: translateY(-100%);
        }
        .btn_control_menu{display: block;}
    }
    .menu_bread{
      display: block;
      overflow-x: scroll;
      overflow-y: hidden;
      -webkit-overflow-scrolling: touch;
      white-space: nowrap;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;      
    }
    .icon_home{
      padding: 0 10px 0 12px;
      float: left;
      line-height: 40px;
      border-right: 1px solid #e5e5e5;
      svg{
        vertical-align: middle;
        path{fill: #222;}
      }
      &.active{
        svg{
          path{fill: #9F224E;}
        }
      }
    }
}
.navigation_scroll{
    overflow-x: scroll;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    white-space: nowrap;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}
.menu_bread{
    display: table-cell;
    vertical-align: middle;
  li{
    display: table-cell;
    a{
      height: 100%;
      display: block;
      font: 400 15px/38px arial !important;
      padding: 0 15px;
      white-space: nowrap;
      position: relative;
        &:after{
                  content: '';
                  width: 1px;
                  height: 20px;
                  background: #ccc;
                  position: absolute; top: 50%; margin-top: -10px; right: -0.5px;
              } 
    }

      &.active{
          a{font-weight: 700 !important;}
      }  
      &.start{
          a{font-weight: 700 !important; color: $vne_color;
              &:after{
                  @extend %ic;
                  content: '';
                  width: 11px;
                  height: 16px;
                  background-position: -3px -126px;
                  position: absolute; top: 50%; margin-top: -8px; right: -6px;
              }
          }
      }
      &:last-child{
          a{
              &:after{display: none;}
          }  
      }

  }
}
/*personalized*/
.nav_user{
  .menu_bread{
    li{
      h4{margin: 0 15px;}
      a{
        color: #222222;padding: 0;position: relative;line-height: 41px!important;
        &:after{display:none;}
        svg{
          path{color: #222222;fill: #222222;}
        }
      }
      &.first{
        border-right:1px solid #e5e5e5;
        h4{margin: 0;}
        a{
          padding: 0 15px;
          svg{width: 16px;height: 16px;vertical-align: middle;}
          .user{width: 24px;height: 24px;vertical-align: middle;}
        }
      }
      &.active{
        a{color: $vne_color;
          &:before{
            content:"";
            background: $vne_color;
            position: absolute;left: 0;
            height: 1px;width: 100%;
          }
        }
        svg{
          path{color: $vne_color;fill: $vne_color;}
        }        
      }
    }
  }
  &.header_cloned{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    overflow: visible;
    box-shadow: 0 1px 3px rgba(80,80,80,0.2), 0 3px 5px rgba(80,80,80,0.15);
    transition-duration: 300ms;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    transform: translateY(0);   
    .menu_bread{
      display: block;
      overflow-x: scroll;
      overflow-y: hidden;
      -webkit-overflow-scrolling: touch;
      white-space: nowrap;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
      li{
        &.active a:before{display:none;}
        &.first{border-right:0;
          a{padding: 0 5px;}
        }
      }
    }
  }
  &.nav-up{
    -webkit-transform: translateY(-100%);
    -ms-transform: translateY(-100%);
    transform: translateY(-100%);  
  }
  &.nav-down{padding-left: 30px;
    .btn_control_menu{display: block;
      .ic{margin-top: 2px;}
    }
  }
}
.nav_user.header_cloned + #top_story{margin-top: 40px;}
